"use client";

import { createContext, useContext, useEffect, useState, ReactNode } from "react";
import { Models } from "appwrite";
import { account, ID } from "@/lib/appwrite";
import { useToast } from "@/hooks/use-toast";

interface AuthContextType {
  user: Models.User<Models.Preferences> | null;
  loading: boolean;
  signUp: (email: string, password: string, fullName: string) => Promise<{ error: Error | null }>;
  signIn: (email: string, password: string) => Promise<{ error: Error | null }>;
  signOut: () => Promise<void>;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export function AuthProvider({ children }: { children: ReactNode }) {
  const [user, setUser] = useState<Models.User<Models.Preferences> | null>(null);
  const [loading, setLoading] = useState(true);
  const { toast } = useToast();

  useEffect(() => {
    checkUser();
  }, []);

  const checkUser = async () => {
    try {
      const accountData = await account.get();
      setUser(accountData);
    } catch (error) {
      setUser(null);
    } finally {
      setLoading(false);
    }
  };

  const signUp = async (email: string, password: string, fullName: string) => {
    try {
      // 1. Create Account
      await account.create(ID.unique(), email, password, fullName);

      // 2. Create Session (Login immediately)
      // If a session exists, delete it first to ensure we log in the new user
      try {
        await account.deleteSession("current");
      } catch (e) {
        // Ignore error if no session exists
      }

      await account.createEmailPasswordSession(email, password);
      
      const accountData = await account.get();
      setUser(accountData);
      
      return { error: null };
    } catch (error: any) {
      console.error("Signup error:", error);
      return { error };
    }
  };

  const signIn = async (email: string, password: string) => {
    try {
      // Check if already logged in
      try {
        const currentAccount = await account.get();
        if (currentAccount.email === email) {
            setUser(currentAccount);
            return { error: null };
        }
        // If logged in as someone else, logout first
        await account.deleteSession("current");
      } catch (e) {
        // Not logged in, proceed
      }

      await account.createEmailPasswordSession(email, password);
      const accountData = await account.get();
      setUser(accountData);
      return { error: null };
    } catch (error: any) {
      console.error("Login error:", error);
      // If error is "Creation of a session is prohibited...", it means we raced or logic failed.
      // Try fetching account one last time to be safe.
      if (error?.type === 'general_session_already_exists') {
          const accountData = await account.get();
          setUser(accountData);
          return { error: null };
      }
      return { error };
    }
  };

  const signOut = async () => {
    try {
      await account.deleteSession("current");
      setUser(null);
    } catch (error: any) {
      toast({
        title: "Error signing out",
        description: error.message,
        variant: "destructive",
      });
    }
  };

  return (
    <AuthContext.Provider value={{ user, loading, signUp, signIn, signOut }}>
      {children}
    </AuthContext.Provider>
  );
}

export function useAuth() {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error("useAuth must be used within an AuthProvider");
  }
  return context;
}
